<template>
  <!-- 添加租户对话框 -->
  <EditForm ref="editFormRef" @refresh="refreshData"/>
  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-card class="search-card" shadow="never">
        <template #header>
          <div class="search-header">
            <span class="search-title">
              <el-icon><Search /></el-icon>
              用户搜索
            </span>
            <el-button type="text" @click="toggleSearch" class="close-btn">
              <el-icon><Close /></el-icon>
            </el-button>
          </div>
        </template>
        
        <el-form :model="searchForm" label-width="100px" class="search-form">
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="昵称">
                <el-input 
                  v-model="searchForm.nickname" 
                  placeholder="请输入昵称" 
                  clearable 
                  prefix-icon="User"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="账号">
                <el-input 
                  v-model="searchForm.username" 
                  placeholder="请输入账号" 
                  clearable 
                  prefix-icon="User"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item>
                <div class="search-buttons">
                  <el-button type="primary" @click="handleSearch" :loading="searchLoading">
                    <el-icon><Search /></el-icon>
                    搜索
                  </el-button>
                  <el-button @click="handleReset">
                    <el-icon><Refresh /></el-icon>
                    重置
                  </el-button>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
    </div>
  </transition>

  <!-- 表头 -->
  <div class="table-header ba-scroll-style">
    <!-- 刷新、添加、编辑、删除和导入导出按钮 -->
    <el-button :icon="Refresh" @click="refreshData"  color="#40485b"/>
    <el-button type="primary" :icon="Plus" @click="openEditFrom">添加</el-button>
    <el-button type="primary" :disabled="!hasSelectedRows">编辑</el-button>
    <el-button type="danger" :disabled="!hasSelectedRows"  @click="handleBatchDelete('/api/v1/admin/user/delete')">删除</el-button>

    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        table-layout="fixed"
        style="width: 100%;" @selection-change="handleSelectionChange">
      <!-- 表格列定义 -->
      <el-table-column type="selection" width="40" />
      <!--      <el-table-column prop="id" label="编号" align="center" min-width="80"/>-->
      <el-table-column prop="nickname" label="昵称" align="center" min-width="150"/>
      <el-table-column prop="username" label="账号" align="center" min-width="150"/>
      <el-table-column prop="group.name" label="所属分组" align="center" min-width="150"/>
      <el-table-column prop="updated_at" label="创建时间" align="center" min-width="150"/>
      <el-table-column prop="weight" label="权重" align="center" min-width="150"/>
      <el-table-column prop="remark" label="备注" align="center" min-width="150"/>

      <!-- 状态列 -->
      <el-table-column label="状态" align="center" min-width="80">
        <template #default="{ row }">
          <el-switch v-model="row.status" :active-value="1" :inactive-value="0" @change="toggleSwitch('/api/v1/admin/user/switch',row.id)"/>
        </template>
      </el-table-column>


      <!-- 操作列 -->
      <el-table-column label="操作" align="center" fixed="right" min-width="300">
        <template #default="scope">
          <el-button size="small"  @click="openEditFrom(scope.row)">编辑</el-button>
          <el-button size="small" type="danger"   @click="handleDelete('/api/v1/admin/user/delete', scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>




  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script lang="ts" setup>
import { Plus, Refresh, Search, Close, User } from '@element-plus/icons-vue';
import { ref, onMounted } from 'vue';


// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";
import EditForm from "./edit.vue";

const editFormRef = ref(null);
const openEditFrom = (row = {}) => {
  console.log(row);
  if (editFormRef.value) {
    editFormRef.value.open({ ...row}); // 传递 `row` 作为参数
  } else {
    console.error("ComplaintForm 组件未正确挂载！");
  }
};
// 使用表格插件来管理表格的状态、数据等
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams,
  deleteRows,
  toggleSwitch,
  resetSearchParams
} = useTableDataPlugin("/api/v1/admin/user/index");

// 搜索表单数据
const searchForm = ref({
  nickname: '',
  username: ''
});

// 搜索加载状态
const searchLoading = ref(false);

// 处理搜索
const handleSearch = async () => {
  searchLoading.value = true;
  try {
    // 将搜索表单数据同步到searchParams
    searchParams.value = { ...searchForm.value };
    await refreshData();
  } catch (error) {
    console.error('搜索失败:', error);
    ElMessage.error('搜索失败，请重试');
  } finally {
    searchLoading.value = false;
  }
};

// 处理重置
const handleReset = () => {
  searchForm.value = {
    nickname: '',
    username: ''
  };
  searchParams.value = { ...searchForm.value };
  refreshData();
};

// 在组件挂载时加载数据
onMounted(() => {
  loadData();
});

// 删除方法：单条删除
const handleDelete = (api: string, row: any) => {
  deleteRows(api,[row.id]);  // 传递一个 id 数组来删除单条记录
};

// 批量删除
const handleBatchDelete = (api: string,) => {
  deleteRows(api);
};

</script>

